import React from 'react'
import {
    Card,
    CardBody,
    FormGroup,
    Button
} from "reactstrap"
export default function Email() {
    return (
        <Card>
            <CardBody>
                <FormGroup>
                    <Button.Ripple color="danger" type="submit">Login using Gmail Account </Button.Ripple>
                </FormGroup>
            </CardBody>
        </Card>
    )
}
